<template>
    <yd-layout class="jianchatiaomu">
        <navBar title="检查条目" slot="navbar"></navBar>
        <div class="list">
            <h4>巡检点名称：{{name}}</h4>
            <p v-for="(item,index) in list" :key="index">{{index+1}}、{{item.xjtmms}}</p>
        </div>
    </yd-layout>
</template>

<script>
    export default {
        name: "jianchatiaomu",
        data:function () {
            return{
                name:window.localStorage.getItem('name'),
                list:JSON.parse(window.localStorage.getItem('tiaomu')),
            }
        },
        mounted(){
          console.log('我是条目',this.list);
        },
    }
</script>

<style scoped>
    .jianchatiaomu{
        background-color: #ffffff;
    }
    .list{
        display: flex;
        flex-direction:column;
    }
    .list h4{
        font-size: .33rem;
        line-height: .8rem;
        border-bottom: 1px solid #999;
        padding: 0 .5rem;
     }
    .list p{
        font-size: .3rem;
        line-height: .8rem;
        border-bottom: 1px solid #999;
        padding: 0 .5rem;
    }
</style>



